body {
  background: #1c1e22;
  font-family: Lato, Segoe Ui, -apple-system, BlinkMacSystemFont, sans-serif;
}

header {
  background-color: #fff;
  padding: 20px;
  box-shadow: 0 2px 5px #0000001a;
}

*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.flip-card-container {
  --hue: 150;
  --primary: hsl(var(--hue), 50%, 50%);
  --white-1: #e6e6e6;
  --white-2: #ccc;
  --dark: hsl(var(--hue), 25%, 10%);
  --grey: gray;
  width: 310px;
  height: 500px;
  perspective: 1000px;
  margin: 40px;
}

.flip-card {
  width: inherit;
  height: inherit;
  transform-style: preserve-3d;
  transition: all .6s .1s;
  position: relative;
}

.flip-card-container:hover .flip-card, .flip-card-container:focus-within .flip-card {
  transform: rotateY(180deg);
}

.card-front, .card-back {
  width: 100%;
  height: 100%;
  background: var(--dark);
  backface-visibility: hidden;
  border-radius: 24px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.card-front {
  z-index: 2;
  transform: rotateY(0);
}

.card-back {
  z-index: 1;
  transform: rotateY(180deg);
}

figure {
  z-index: -1;
}

figure, .img-bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

img {
  height: 100%;
  border-radius: 24px;
}

figcaption {
  width: auto;
  letter-spacing: 2px;
  word-spacing: 6px;
  text-align: right;
  color: var(--white-1);
  background: hsla(var(--hue), 25%, 10%, .5);
  margin-top: 12%;
  padding: 8px 22px;
  font-weight: bold;
  line-height: 1.6;
  display: block;
  position: absolute;
  top: 0;
  right: 12px;
}

.img-bg {
  background: hsla(var(--hue), 25%, 10%, .5);
}

.card-front .img-bg {
  clip-path: polygon(0 20%, 100% 40%, 100% 100%, 0 100%);
}

.card-front .img-bg:before {
  content: "";
  width: 100%;
  height: 6px;
  border: 1px solid var(--primary);
  border-left-color: #0000;
  border-right-color: #0000;
  transition: all .1s;
  position: absolute;
  top: 34%;
  left: 50%;
  transform: translate(-50%, -50%)rotate(18deg);
}

.card-back .img-bg {
  clip-path: polygon(0 0, 100% 0, 100% 80%, 0 60%);
}

.flip-card-container:hover .card-front .img-bg:before {
  width: 6px;
  border-left-color: var(--primary);
  border-right-color: var(--primary);
}

button {
  color: var(--white-1);
  letter-spacing: 2px;
  border: 1px solid var(--grey);
  cursor: pointer;
  background: none;
  border-radius: 1000px;
  padding: 9px 20px;
  font-family: inherit;
  font-weight: bold;
  transition: all .3s;
}

button:hover, button:focus {
  color: var(--primary);
  background: hsla(var(--hue), 25%, 10%, .2);
  border-color: currentColor;
}

button:active {
  transform: translate(2px);
}

.card-wrapper {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.blog-card {
  background: #fff;
  flex-direction: column;
  display: flex;
  position: relative;
  box-shadow: 0 10px 50px #fc38384d;
}

button {
  height: 50px;
  justify-content: center;
  padding: 10px 20px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.inner-part::-webkit-scrollbar {
  width: 0;
  height: 0;
  overflow-x: scroll;
}

.inner-part {
  height: 100%;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  padding: 0 25px 60px;
  display: flex;
  position: relative;
  overflow-y: auto;
}

#imgTap:checked ~ .inner-part {
  padding: 0;
  transition: all .1s ease-in;
}

.inner-part .img {
  height: 260px;
  width: 260px;
  float: left;
  border-radius: 20px;
  flex-shrink: 0;
  margin-right: 20px;
  overflow: hidden;
  box-shadow: 2px 3px 15px #fc38381a;
}

.img img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  cursor: pointer;
  opacity: 0;
  transition: all .6s;
}

#tap-1:checked ~ .inner-part .img-1, #tap-2:checked ~ .inner-part .img-2, #tap-3:checked ~ .inner-part .img-3 {
  opacity: 1;
  transition-delay: .2s;
}

.content {
  width: 530px;
  opacity: 0;
  align-items: center;
  margin-left: 100px;
  padding: 0 20px 0 35px;
  transition: all .6s;
}

#imgTap:checked ~ .inner-part .content {
  display: none;
}

#tap-1:checked ~ .inner-part .content-1, #tap-2:checked ~ .inner-part .content-2, #tap-3:checked ~ .inner-part .content-3 {
  opacity: 1;
  z-index: 100;
  margin-left: 0;
  transition-delay: .3s;
}

.content span {
  color: #7b7992;
  justify-content: flex-end;
  margin-bottom: 15px;
  font-size: 22px;
  font-weight: 500;
  display: block;
}

.content .title {
  color: #0d0925;
  font-size: 20px;
  font-weight: 700;
}

.content .text {
  color: #4e4a67;
  text-align: justify;
  font-size: 19px;
}

.content button {
  text-transform: uppercase;
  color: #fff0e6;
  letter-spacing: 1px;
  cursor: pointer;
  background: linear-gradient(147deg, #fe8a39 0%, #fd3838 74%);
  border: 1px solid #fd3535;
  border-radius: 50px;
  outline: none;
  padding: 15px 20px;
  font-size: 16px;
  font-weight: 600;
  display: inline-flex;
}

.content button:hover {
  background: linear-gradient(147deg, #fe791b 0%, #fd1c1c 74%);
}

#imgTap:checked ~ .sliders, input[type="radio"], input[type="checkbox"] {
  display: none;
}

